  <template>
  <div class="app">
    <h1 class="app__title">
      Конфигуратор Инфраструктуры
    </h1>
    
  <div class="app__buttons">
   <input type="radio" id="radio1" name="radios" v-model="result" v-bind:value="a"> 
   <label for="radio1">Small</label> 
   <input type="radio" id="radio2" name="radios" v-model="result" v-bind:value="b"> 
   <label for="radio2">Medium</label> 
   <input type="radio" id="radio3" name="radios" v-model="result" v-bind:value="c"> 
   <label for="radio3">Large</label>
  </div>


<!--
<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      a:126000,
      b:34000,
      c:78030,
      result:0
    }
  }
}

</script>

-->

  <div class="bg">
    <div class="ServicesAndLicenses">
    <span class="ServicesAndLicenses__title">Сервисы и лицензии</span>
    <input  class="ServicesAndLicenses__switcher__input" type="checkbox" name="watched" id="switcher" v-model="checked" @click="greet()">
      <label class="ServicesAndLicenses__switcher__label" for="switcher">
        <span class="switcher__text">Microsoft Windows</span> 
      </label>
  </div>
  </div>
<!--
<script>
export default {
  name: 'HelloWorld',
  props:[checked, price],
  methods:{
  greet: function () {
    if (!this.checked) {
      this.price = 5000
    } else {
      this.price = 0
    }
  }
}
}
</script>
-->

  <div class="tariff">
    <div class="tariff__in">
      <div class="tariff__in__tariff__title">Гарантированный</div>
      <div class="tariff__in__price">{{ price + result + 63400 }},00 ₽</div>
      <div class="tariff__in__in_date">В месяц</div>
    </div>
    <div class="tariff__in">
      <div class="tariff__in__tariff__title">Гибкий</div>
      <div class="tariff__in__price">{{ price + result + 33600 }},00 ₽</div>
      <div class="tariff__in__in_date">В месяц</div>
    </div>
  </div>

</div>
</template>
<style>

.app__buttons {
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;

  width: 250px;
}

input[type=radio] + label { 
    display:inline-block; 
    padding: 20px 15px; 
    background-color: #fafafa; 
} 
input[type=radio]:checked + label {
    background-image: none; 
    background-color:#D03730; 
    color: #FAFAFA
}

input[type=radio] { 
    display:none; 
} 

label {
    border: 1px solid #D03730; 
    border-radius: 10px;
    font-family: 'Roboto Condensed';
    font-weight: 300;
}

.bg {
  background-color: #EEEEEE;
  padding: 30px 30px; 
  width:93%;
  margin: 30px auto;
  border-radius: 20px; 
}

body {
    margin: 0;
    font-family: Arial, sans-serif;
    font-size: 16px;
}

.ServicesAndLicenses {
  width: 200px;
  margin: 0 auto;
}

.ServicesAndLicenses__title {
  margin-left: 0px;
  font-size: 20px;
  margin-bottom: 20px;
}


.switcher__title {
    margin: 0 0 20px;
}


.ServicesAndLicenses__switcher__input {
    display: none;
}



.ServicesAndLicenses__switcher__input:checked + .ServicesAndLicenses__switcher__label:before {
    background-color: #D03832;
}

.ServicesAndLicenses__switcher__input:checked + .ServicesAndLicenses__switcher__label:after {
    transform: translateX(23px);
}

.ServicesAndLicenses__switcher__input:checked + .ServicesAndLicenses__switcher__label:active:after {
    transform: translateX(12px);
}

.ServicesAndLicenses__switcher__input:checked + .ServicesAndLicenses__switcher__label:active:before {
    background-color: #D03832;
}

.ServicesAndLicenses__switcher__label {
    display: inline-block;
    vertical-align: top;
    padding-left: 50px;

    font-size: 12px;
    cursor: pointer;

    position: relative;

    transition: color .1s linear;
    border: none;
    margin-top: 20px;
}

.ServicesAndLicenses__switcher__label:before {
    content: "";
    display: block;
    width: 65px;
    height: 40px;

    background-color: #e5e5e5;
    border-radius: 20px;

    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;

    transition: background-color .1s linear;
}

.ServicesAndLicenses__switcher__label:after {
    content: "";
    display: block;
    width: 32px;
    height: 32px;

    background-color: #fff;
    border-radius: 50%;

    position: absolute;
    top: 4px;
    left: 5px;
    z-index: 2;

    transition: transform .1s linear;
}

.ServicesAndLicenses__switcher__label:active:after {
    transform: translateX(20px);
}

.ServicesAndLicenses__switcher__label:active:before {
    background-color: #D03832;
}

.switcher__text {
  margin-left: 30px; 
    line-height: 40px;
}

  .tariff {
    display: flex;
    margin-top: 30px;
    margin-bottom: 30px;
  }

  .tariff__in {
    margin: 0 auto;
    width: 600px;
    height: 170px;
    box-shadow: 0px  10px 20px rgba(0,0,0,0.1);  /* Параметры тени */
  }

  .tariff__in__tariff__title {
    font-size: 19px;
    margin: 40px 20px;
    margin-bottom: 10px;
  }

  .tariff__in__price {
    font-size: 34px;
    margin: 0px 20px;
  }

  .tariff__in__in_date {
    margin: 10px 20px;
    color: #B1B1B1;
  }


</style>
<script>

export default {
  name: 'app',
  data () {
    return {
      checked:false,
      price:'',
      a:126000,
      b:34000,
      c:78030,
      result:0
  }
},
methods:{
  greet: function () {
    if (!this.checked) {
      this.price = 5000
    } else {
      this.price = 0
    }
  }
}
}
</script>

<style>
  
  html, body {
    padding:0;
    margin: 0;
  }

  .app {
    margin: 20px;

    border: 4px dotted gray;
    border-radius: 20px;
  }

  .app__title {
    padding: 10px 0;
    margin: 0;

    font-family: 'Roboto Condensed';
    font-weight: 300;

    text-align: center;
  }

</style>



